import "~/extension/HC2DDom";
import "~/extension/Hc2DGui";
import "~/extension/HC2DTexture";

import { AppConfig, Application, AppStore, CameraType } from "@hc/engine";
import React, { useEffect, useRef, useState } from "react";
import { createRoot } from "react-dom/client";

import { DirSourceType, FileApi, MarketApi, ProjectApi } from "./apis";
import { setAppIntance, setPrefix } from "./intance";
import {
  deleteReq,
  getReq,
  myAxios,
  postReq,
  ResponseStatus,
} from "./utils/axios";
import { getUniupComponentType, registerComponent } from "./utils/component";
import { loadOnlineFile } from "./utils/loadFile";
import { MarkList, PreviewData } from "./apis/types";
import { ECategory } from "./components/resourceManager/types";
import {
  Alert,
  AlertColor,
  Box,
  Button,
  LinearProgress,
  LinearProgressProps,
  Snackbar,
  Typography,
} from "@mui/material";
import { previewChannel } from "./utils/previewChannel";
import ConfirmModal from "./components/common/confirm";
import BuyItemPanel from "./components/resourceManager/buyItem";
import { deleteQueryKey } from "./utils/url";
import { setToken } from "./utils/auth";

interface PreviewProps {}

AppConfig.assetURL = ASSET_URL;
AppConfig.Texture_URL = "https://am-img.gkiiot.com/editor/";
AppConfig.SkyBox_URL = "https://am-img.gkiiot.com/editor/";

window["IGNORE_INVAILID_TOKEN"] = true;

let timer;
let app: Application;

const Preview: React.FC<PreviewProps> = () => {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const [url2d, setUrl2d] = useState("");
  const [detail, setDetail] = useState<MarkList>(null);
  const [startBuy, setStartBuy] = useState(false);
  const [frame, setFrame] = useState(false);
  const [progress, setProgress] = useState({ start: false, value: 0 });
  const [toaster, setToaster] = useState({
    open: false,
    msg: "",
    type: "success" as AlertColor,
  });
  const [roam, setRoam] = useState({ disable: true, open: false });

  const sendMessage = (data) => {
    previewChannel.postMessage(data);
    if (window.opener) window.opener.postMessage(data, "*");
  };

  const collect = async () => {
    if (detail.Collects) {
      let res = await deleteReq(MarketApi.cancelCollect + detail.product_code);
      if (res.code === ResponseStatus.Ok) {
        setDetail({ ...detail, Collects: false });
        setToaster({ ...toaster, open: true, msg: "取消收藏成功" });
        previewChannel.postMessage({ type: "update-list" });
      } else {
        if (res.error?.[0]) {
          setToaster({ type: "error", open: true, msg: res.error?.[0] });
        }
      }
    } else {
      let res = await postReq(MarketApi.collect, {
        productCode: detail.product_code,
      });
      if (res.code === ResponseStatus.Ok) {
        setDetail({ ...detail, Collects: true });
        setToaster({ ...toaster, open: true, msg: "商品收藏成功" });
        previewChannel.postMessage({ type: "update-list" });
      } else {
        if (res.error?.[0]) {
          setToaster({ type: "error", open: true, msg: res.error?.[0] });
        }
      }
    }
  };

  const buy = async () => {
    let res = await postReq(MarketApi.buy, {
      productCode: detail.product_code,
    });
    if (res.code === ResponseStatus.Ok) {
      setToaster({ ...toaster, open: true, msg: "商品购买成功" });
      setStartBuy(false);
      setDetail({ ...detail, Boughten: true });
      previewChannel.postMessage({ type: "update-list" });
    } else {
      if (res.error?.[0]) {
        setToaster({ type: "error", open: true, msg: res.error?.[0] });
      }
    }
  };
  const apply = () => {
    const url = new URLSearchParams(location.search);
    const market = url.get("market");
    const code = url.get("code");
    if (market) {
      if (code) location.href = location.origin + "/editor?code=" + code;
    } else {
      setToaster({
        ...toaster,
        open: true,
        msg: "准备开始应用该素材，请回到源网站操作",
      });
      sendMessage({ type: "apply-shop", shop: detail });
    }
  };

  const loadModel = async (): Promise<boolean> => {
    const url = new URLSearchParams(location.search);
    const id = url.get("model");

    if (id) {
      const result = await getReq(FileApi.detail + "/" + id);
      if (result.code === ResponseStatus.Ok) {
        const info = result.data.info;
        const url = info?.uuid + "/" + info.file_key;
        app.create({ type: "ImportEntity", url });
        return true;
      }
    }
    return false;
  };

  const endLoad = () => {
    timer && clearInterval(timer);
    setProgress({ start: false, value: 0 });
  };

  const renderComponent = (json: string, id: number) => {
    const jsonData = JSON.parse(json);
    const d = jsonData.data;
    registerComponent({ ...d, id });
    // const url = info?.uuid + "/" + info.file_key;
    app.create({ type: getUniupComponentType(id) });
  };

  const loadComponent = async (): Promise<boolean> => {
    const url = new URLSearchParams(location.search);
    const id = url.get("component");

    if (id) {
      const result = await getReq(FileApi.detail + "/" + id);
      if (result.code === ResponseStatus.Ok) {
        const info = result.data.info;
        const json = info.json_data;
        renderComponent(json, info.id);
        return true;
      }
    }
    return false;
  };

  const loadShopByCode = async (shopCode, isLogin: boolean) => {
    const detailRes = await getReq<MarkList>(
      isLogin ? MarketApi.detailEx : MarketApi.detail,
      {
        params: { product_code: shopCode },
      }
    );
    console.log("detail: ", detailRes);

    if (detailRes.code === ResponseStatus.Ok) {
      const detail = detailRes.data;
      setDetail(detail);
      window["setEcommerceProductView"]?.(
        detail.product_code,
        detail.product_name,
        detail.real_type,
        detail.score
      );
      const res = await getReq<PreviewData>(
        MarketApi.preview + detail.real_code
      );
      if (res.code === ResponseStatus.Ok) {
        switch (res.data.source_type) {
          case DirSourceType.Files:
          case "project":
          case "app": {
            if (!res.data.json_data) {
              if (res.data.data.endsWith(".json")) {
                setToaster({
                  open: true,
                  msg: "暂不支持预览2D项目",
                  type: "error",
                });
              }
              endLoad();
              return false;
            }
            if (res.data.variable) {
              AppStore.GlobalVariables = JSON.parse(res.data.variable);
            }
            const data = JSON.parse(res.data.json_data);
            setUrl2d(data.url2d);
            loadOnlineFile(data, myAxios, app);
            setRoam({
              disable: false,
              open: app.Viewer.cameraManager.Type === CameraType.FreeCamera,
            });
            endLoad();
            break;
          }
          case DirSourceType.Component: {
            app.Viewer.sceneControl.createDefaultEnv();
            renderComponent(res.data.json_data, Date.now());
            endLoad();
            break;
          }
          case DirSourceType.Model: {
            app.Viewer.sceneControl.createDefaultEnv();
            const url = res.data.data;
            const en = app.create({ type: "ImportEntity", url });
            en.on("load", () => {
              setTimeout(() => {
                app.zoomAll();
                endLoad();
              }, 100);
            });
            break;
          }
        }
        return true;
      } else {
        if (!isLogin) {
          setToaster({ type: "error", open: true, msg: "商品加载出错" });
          endLoad();
        }
      }
    }
    return false;
  };

  const loadShop = async () => {
    const url = new URLSearchParams(location.search);
    timer = setInterval(() => {
      setProgress((prevProgress) => {
        let value = prevProgress.value;
        if (value < 90) value += 5;
        else {
          if (value < 99) {
            value += 1;
          }
        }
        return {
          start: true,
          value,
        };
      });
    }, 800);

    setFrame(!!url.get("frame"));
    const shopCode = url.get("code");

    const token = url.get("token");
    if (token) {
      deleteQueryKey(location.href, "token", true);
      setToken(token);
    }

    if (shopCode) {
      const status = await loadShopByCode(shopCode, true);
      if (!status) {
        await loadShopByCode(shopCode, false);
      }
      return true;
    }
    return false;
  };
  const getType = (type: string) => {
    if (type === ECategory.Component || type === ECategory.Model) {
      return "模型";
    } else if (type === ECategory.File) {
      return "场景";
    } else if (type === ECategory.Project) {
      return "项目";
    }

    return type;
  };

  const toggleRoam = () => {
    setRoam({ disable: false, open: !roam.open });
    app.Viewer.cameraManager.Type = !roam.open
      ? CameraType.FreeCamera
      : CameraType.ArcRotate;
  };

  useEffect(() => {
    (async () => {
      await setPrefix();
      if (canvasRef.current) {
        const canvas = canvasRef.current;
        canvas.width = canvas.parentElement.clientWidth;
        canvas.height = canvas.parentElement.clientHeight;

        window["app"] = app = new Application({
          canvas,
          editor: false,
          cameraRadius: 100,
        });
        setAppIntance(app);

        let status = await loadModel();

        if (status) return;

        status = await loadComponent();
        if (status) return;

        status = await loadShop();
        if (status) return;
      }
    })();
  }, []);

  return (
    <Box style={{ width: "100%", height: "100%" }}>
      {/* <iframe
        src={url2d}
        style={{
          width: "100%",
          height: "100%",
          pointerEvents: "none",
          backgroundColor: "transparent",
          border: "0",
          position: "fixed",
        }}
      ></iframe> */}
      <canvas style={{ width: "100%", height: "100%" }} ref={canvasRef}>
        预览页面
      </canvas>
      {detail && (
        <Box style={{ position: "fixed", top: "5%", right: "5%" }}>
          <Box style={{ marginBottom: 10 }}>
            {detail.Collects !== undefined && (
              <Button onClick={collect} variant="contained" sx={{ mr: 1 }}>
                {detail.Collects ? "取消收藏" : "收藏"}
              </Button>
            )}
            {detail.Boughten !== undefined && !detail.Boughten && (
              <Button
                onClick={() => setStartBuy(true)}
                variant="contained"
                sx={{ mr: 1 }}
              >
                购买
              </Button>
            )}
            {detail.Boughten && (
              <Button
                onClick={apply}
                variant="contained"
                color="success"
                sx={{ mr: 1 }}
              >
                应用
              </Button>
            )}

            {!frame && window.opener && (
              <Button
                onClick={() => {
                  sendMessage({ type: "close_preview" });
                }}
                variant="contained"
                sx={{ mr: 1 }}
              >
                关闭
              </Button>
            )}
            {!roam.disable && (
              <Button variant="contained" onClick={toggleRoam}>
                {roam.open ? "关闭漫游" : "开启漫游"}
              </Button>
            )}
          </Box>
          <Box>
            <Typography style={{ marginBottom: 5 }}>
              素材名称:{detail.product_name}
            </Typography>
            <div style={{ marginBottom: 5 }}>
              类型:{getType(detail.real_type)}
            </div>
            <div style={{ marginBottom: 5 }}>作者:{detail.possessor_code}</div>
            <div style={{ marginBottom: 5 }}>积分:{detail.score}</div>
            <div style={{ marginBottom: 5 }}>更新时间:{detail.update_at}</div>
            {!roam.disable && (
              <div style={{ marginBottom: 5 }}>
                4.漫游模式下，WASD控制移动方向，鼠标控制视角
              </div>
            )}
          </Box>
        </Box>
      )}
      <Box style={{ position: "fixed", bottom: "5%", left: "5%" }}>
        <Typography style={{ marginBottom: 5 }}>帮助说明</Typography>
        <Typography style={{ marginBottom: 5 }}>
          1.按住鼠标滚轮进行旋转
        </Typography>
        <Typography style={{ marginBottom: 5 }}>2.滚动滚轮进行缩放</Typography>
        <Typography style={{ marginBottom: 5 }}>
          3.Alt+鼠标中键移动视角
        </Typography>
      </Box>
      {progress.start && (
        <Box
          style={{
            position: "fixed",
            left: 0,
            right: 0,
            bottom: 0,
            top: 0,
            width: "50%",
            height: "30px",
            margin: "auto",
          }}
        >
          <LinearProgressWithLabel
            sx={{ height: 15, borderRadius: 5 }}
            value={progress.value}
          />
        </Box>
      )}
      <Snackbar
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        open={toaster.open}
        autoHideDuration={2000}
        onClose={() => setToaster({ ...toaster, open: false })}
        key="toaster"
      >
        <Alert
          onClose={() => setToaster({ ...toaster, open: false })}
          severity={toaster.type}
          sx={{ width: "100%" }}
        >
          {toaster.msg}
        </Alert>
      </Snackbar>
      {startBuy && (
        <ConfirmModal
          open={startBuy}
          onOk={buy}
          onCancel={() => {
            setStartBuy(false);
          }}
          content={<BuyItemPanel shop={detail} />}
          title="您即将购买"
        />
      )}
    </Box>
  );
};

const root = createRoot(document.getElementById("app"));

root.render(<Preview />);

function LinearProgressWithLabel(
  props: LinearProgressProps & { value: number }
) {
  return (
    <Box sx={{ display: "flex", alignItems: "center" }}>
      <Box sx={{ width: "15%" }}>正在加载资源：</Box>
      <Box sx={{ width: "75%", mr: 1 }}>
        <LinearProgress variant="determinate" {...props} />
      </Box>
      <Box sx={{ width: "10%" }}>
        <Typography variant="body2" color="text.secondary">{`${Math.round(
          props.value
        )}%`}</Typography>
      </Box>
    </Box>
  );
}
